import React from "react";
import cx from "classnames";
import styles from "./Header.module.scss";

type HeaderTheme = {
  backgroundColor: string;
  color: string;
  accentColor: string;
};

type HeaderProps = {
  theme: HeaderTheme;
};

const Header = ({ theme }: HeaderProps) => {
  const { backgroundColor, color, accentColor } = theme;
  const classes = cx(styles.header, {});

  const headerStyle = {
    "--accent-color-primary": accentColor || "#0078f2", 
    backgroundColor, // This directly sets the style, not via a custom property
    color,
  };

  return <header className={classes}></header>;
};

export default Header;
